<template>
    <yd-layout :class="'unionCot'">
        <yd-navbar title="部门收文" bgcolor="#70b6ef" color="#fff" fontsize="0.36rem">
            <router-link to="/" slot="left">
                <yd-navbar-back-icon color="#fff">
                    <slot color="#fff">返回</slot>
                </yd-navbar-back-icon>
            </router-link>
        </yd-navbar>
        <div class="mainContent handle_main new_label" v-show="mainFlag">
            <div class="mainContentFluid">
                <div class="fluidWidth leaderAdvice">
                    <yd-flexbox direction="vertical">
                        <yd-flexbox-item :id="'scroll_box'">
                            <yd-flexbox>
                                <div class="hint" v-text='subTitle'></div>
                            </yd-flexbox>
                            <yd-flexbox v-if="huanji != '' || miji != ''">
                                <div class="btn_label">
                                    <span class="jijian" v-if="huanji != ''" v-text="huanji"></span><span class="shangmi" v-if="miji != ''" v-text="miji"></span>
                                </div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                    </yd-flexbox>
                    <yd-flexbox direction="vertical" :class="'hasBor'">
                        <yd-flexbox-item :class="'bg_e2'">
                            <ul class="tab_box">
                                <li @click="tab_index = 0" v-bind="{ id : isLeader ? 'js_gaiyao_lead' : 'js_gaiyao' }">
                                    <img src="../../../static/images/label/label_txt.png">
                                    <p>来文信息</p>
                                </li>
                                <li v-bind="{ id : isLeader ? 'js_maintext_lead' : 'js_maintext' }">
                                    <a @click="($route.params.isDo != 1 && maintext == 'javascript:;')  ? textDialog() : ($route.params.isDo == 1) ? maintext = 'javascript:;' : page_jump1(maintext)">
                                        <img v-if="$route.params.isDo == 1" src="../../../static/images/label/label_pdf_shadow.png">
                                        <img v-else src="../../../static/images/label/label_pdf.png">
                                        <p>正 文&nbsp;</p>
                                    </a>
                                </li>
                                <li v-bind="{ id : isLeader ? 'js_file_lead' : 'js_file' }" @click="files.length > 0 && $route.params.isDo == 0 ? tab_index = 2 : tab_index = 0">
                                    <img v-if="files.length > 0 && $route.params.isDo == 0" src="../../../static/images/label/label_doc.png">
                                    <img v-else src="../../../static/images/label/label_doc_shadow.png">
                                    <p> 附 件</p>
                                </li>
                            </ul>
                        </yd-flexbox-item>
                        <yd-flexbox-item>
                            <div class="tab_content bg_box minTabContent" v-if="tab_index == 0">
                                <yd-flexbox>
                                    <div class="range">来文单位：</div>
                                    <div class="range_txt" style="color:#333;" v-text='laiwendanwei'></div>
                                </yd-flexbox>
                                <yd-flexbox>
                                    <div class="range">来文时间：</div>
                                    <div class="range_txt" style="color:#333;" v-text='shouwenriqi'></div>
                                </yd-flexbox>
                                <yd-flexbox>
                                    <div class="range">文&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;号：</div>
                                    <div class="range_txt" style="color:#333;" v-text='wenhao'></div>
                                </yd-flexbox>
                            </div>
                            <div class="tab_content bg_box bg_box2" v-if="tab_index == 2" :class="{active: !fileHidden }">
                                <div class="over">
                                    <div class="range_txt file" v-for='(file, index) in files' @click="queryText(index)" v-text="file"></div>
                                    <a class="open" v-show="files.length > 3" @click="fileHidden = !fileHidden">{{ fileHidden ? '展开附件' : '收起附件'}}</a>
                                </div>
                            </div>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="bmapproveopinions.length > 0">
                            <yd-flexbox>
                                <div class="range">部门领导批示：</div>
                            </yd-flexbox>
                            <yd-flexbox>
                                <div class="opinionOwnBox">
                                    <div class="label_box" v-for="item in bmapproveopinions">
                                        <div class="range_txt flex">
                                            <div class="opName fl nowrap" v-if="item.userName1 != ''" v-text='item.userName1 + "："'></div>
                                            <div class="fl opNeirong" v-text="item.neirong"></div>
                                        </div>
                                        <div class="range opTime" v-text='getDay(item.submitDate)'></div>
                                    </div>
                                </div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="bmnibanopinions.length > 0">
                            <yd-flexbox>
                                <div class="range">综合室拟办：</div>
                            </yd-flexbox>
                            <yd-flexbox>
                                <div class="opinionOwnBox">
                                    <div class="label_box" v-for="item in bmnibanopinions">
                                        <div class="range_txt flex">
                                            <div class="opName fl nowrap" v-if="item.userName1 != ''" v-text='item.userName1 + "："'></div>
                                            <div class="fl opNeirong" v-text="item.neirong"></div>
                                        </div>
                                        <div class="range opTime" v-text='getDay(item.submitDate)'></div>
                                    </div>
                                </div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="bmshiopinions.length > 0">
                            <yd-flexbox>
                                <div class="range">室意见：</div>
                            </yd-flexbox>
                            <yd-flexbox>
                                <div class="opinionOwnBox">
                                    <div class="label_box" v-for="item in bmshiopinions">
                                        <div class="range_txt flex">
                                            <div class="opName fl nowrap" v-if="item.userName1 != ''" v-text='item.userName1 + "："'></div>
                                            <div class="fl opNeirong" v-text="item.neirong"></div>
                                        </div>
                                        <div class="range opTime" v-text='getDay(item.submitDate)'></div>
                                    </div>
                                </div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item :class="'noBox'" v-if="bmshiopinions.length <= 0 && bmnibanopinions.length <= 0 && bmapproveopinions.length <= 0">
                            <div class="noMessage"><img src="../../../static/images/noMessage.png">
                                <p>暂无领导批示</p>
                            </div>
                        </yd-flexbox-item>
                    </yd-flexbox>
                </div>
            </div>
        </div>
        <div class="mainContent1" v-show="sendFlag">
            <fill-postscript v-if="shenpiNum > 0 && !branchFlag" v-on:botSetflag="setFlag" :title="subTitle" :num="shenpiNum"></fill-postscript>
            <branch-Fillpostscript v-if="shenpiNum > 0 && branchFlag" v-on:botSetflag="setFlag" :num="shenpiNum"></branch-Fillpostscript>
        </div>
        <div class="mainContent1" v-show="piyueFlag">
            <assist-approve v-if="liuzhuanNum > 0" v-on:botSetflag="setFlag" :title="subTitle" :num="liuzhuanNum"></assist-approve>
        </div>
        <yd-tabbar v-if="$route.params.isDo == 0 && mainFlag" :class="'skip_box'" slot="tabbar" bgcolor="#61b3f6" color="#fff" size=".3rem">
            <div class="skip_btn">
                <a id="js_piyue_lead" v-if="approveFlag && toggleFlag" @click="setFlag(1)">
                    <div class="">批&nbsp;&nbsp;&nbsp;&nbsp;阅</div>
                </a>
                <a id="js_piyue_lead" v-else-if="!approveFlag && toggleFlag" @click="setFlag(1)">
                    <div class="">批&nbsp;&nbsp;&nbsp;&nbsp;阅</div>
                </a>
                <a id="js_piyue_lead" v-else-if="approveFlag && !toggleFlag" @click="setFlag(0)">
                    <div class="">批&nbsp;&nbsp;&nbsp;&nbsp;阅</div>
                </a>
                <a v-else id="js_fasong" @click="setFlag(1)">
                    <div class="">发&nbsp;&nbsp;&nbsp;&nbsp;送</div>
                </a>
            </div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex';
import fillPostscript from './fillPostscript.vue';
import branchFillpostscript from './branchFillPostscript.vue';
import assistApprove from '../assist/assistApprove.vue';
import axios from 'axios';
export default {
    name: 'unionPayReceived',
    components: {
        fillPostscript,
        assistApprove,
        branchFillpostscript
    },
    data() {
        return {
            subTitle: '',
            miji: '',
            huanji: '',
            summary: '',
            opinion_index: 0,
            textHidden: true,
            fileHidden: true,
            tab_index: 0,
            approveFlag: false,
            laiwendanwei: '',
            shouwenriqi: '',
            zhubanbumen: '',
            xiebanbumen: '',
            chuanyuebumen: '',
            xiebanopinions: [],
            bmshiopinions: [], // 室意见
            bmnibanopinions: [], // 综合室
            chuanyueopinions: [],
            chengbanqixian: '',
            files: '',
            toggleFlag: false,
            navflag: false,
            fileLength: 0,
            shenpiNum: 0,
            maintext: 'javascript:;',
            liuzhuanNum: 0,
            bmapproveopinions: [],
            zjgwopinions: [],
            bgsnibanopinions: [],
            zbbmOpinions: [],
            file_url: [],
            isLeader: false,
            mainFlag: true, // 显示主页面
            sendFlag: false, // 显示发送页面
            piyueFlag: false, // 显示批阅页面
            cainayijian: '', // 办理结果
            branchFlag: false, // 是否是分公司
            wenhao: '',
            caibumen: '',
            caipeople: '',
            caishijian: '',
            errorTime: 0
        }
    },
    mounted() {
        let vm = this;
        vm.pageRender();
        vm.setTitleName('部门收文');
        document.setTitle('部门收文');
        setTimeout(function() {
            var title = '部门收文';
            if (UPCHAT.M.isNative()) {
                UPCHAT.M.init(vm.setNavigationBarTitle(title));
            }
        }, 200);
        vm.errorTime = 0;
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo',
            handleInfo: 'getHandleInfo'
        }),
        ...mapGetters(['getNewsInfo'])
    },
    methods: {
        ...mapMutations([
            'setHandleInfo', 'setTitleName'
        ]),
        pageRender() {
            var vm = this;
            if (Number(vm.userInfo.role) == 2) {
                vm.isLeader = true;
            }
            // 判断是否是分公司签报的发送页面
            vm.userInfo.deptname.indexOf('分公司') == -1 ? vm.branchFlag = false : vm.branchFlag = true;
            vm.setHandleInfo({ activityDefId: vm.$route.params.activeId, keepAlive: [], doApprove: '' });
            if (vm.handleInfo.keepData != '' && vm.handleInfo.keepData.workId == vm.$route.params.workItemID) {
                vm.Render(vm.handleInfo.keepData);
                vm.file_url = vm.handleInfo.file_url;
                if (vm.handleInfo.mainText == '') {
                    vm.loadText();
                } else {
                    vm.maintext = vm.handleInfo.mainText;
                }
                if (vm.file_url.length < vm.files.length && vm.files.length > 0) {
                    vm.loadFile();
                }
            } else {
                vm.$dialog.loading.open('加载中...');
                const promise1 = new Promise(function(resolve, reject) {
                    vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryForm', {
                        'type': vm.$route.params.type,
                        'workItemID': vm.$route.params.workItemID,
                        'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
                    }, function(data1) {
                        if (data1.isSuccess == 'false') {
                            vm.errorTime++;
                            if (vm.errorTime < 3) {
                                vm.pageRender();
                            } else {
                                vm.$dialog.loading.close();
                                vm.$dialog.toast({
                                    mes: '表单未加载成功',
                                    timeout: 2000,
                                    callback: () => {
                                        vm.$router.push('/handle');
                                    }
                                });
                            }
                        } else if (data1.isSuccess == 'true') {
                            vm.$dialog.loading.close();
                        }
                        if (data1.jsonData.CURRENTSTATE == 10 || vm.$route.params.isDo == 1) {
                            vm.Render(data1.jsonData);
                            resolve(true);
                        } else if (data1.jsonData.CURRENTSTATE != 10 && vm.$route.params.isDo != 1 && data1.isSuccess == 'true') {
                            vm.$dialog.toast({
                                mes: '该流程已被处理',
                                timeout: 2000,
                                callback: () => {
                                    setTimeout(function() {
                                        vm.$router.push('/handle');
                                    })
                                }
                            });
                        }
                    }, function(res) {
                        if (res.response.status != 200) {
                            vm.errorTime++;
                            if (vm.errorTime < 3) {
                                vm.pageRender();
                                console.log(vm.errorTime);
                            } else {
                                vm.$dialog.loading.close();
                                vm.$dialog.toast({
                                    mes: '表单未加载成功',
                                    timeout: 2000,
                                    callback: () => {
                                        vm.$router.push('/handle');
                                    }
                                });
                            }
                        }
                    });
                });
                promise1.then(function(data) {
                    vm.loadText();
                    vm.loadFile();
                });
            }
        },
        loadText() {
            const vm = this;
            // 阅读正文附件接口
            axios.get('/portalMgmt/oa/menus/getPdf', {
                params: {
                    'type': vm.$route.params.type,
                    'resultType': 'queryDocByWorkItemIDResult',
                    'workItemID': vm.$route.params.workItemID,
                    'workTitle': vm.subTitle,
                    'random': Math.floor(Math.random() * (100000 + 1))
                }
            }).then(function(data4) {
                vm.maintext = data4.data.jsonData == null ? 'javascript:;' : data4.data.jsonData.pdfUrl;
                vm.setHandleInfo({ mainText: vm.maintext });
            });
        },
        loadFile() {
            let vm = this;
            vm.files.forEach(function(item, index) {
                if (item.indexOf('.rar') != -1) {
                    vm.file_url[index] = '.rar';
                    vm.setHandleInfo({ file_url: vm.file_url });
                } else if (item.indexOf('.zip') != -1) {
                    vm.file_url[index] = '.zip';
                    vm.setHandleInfo({ file_url: vm.file_url });
                } else {
                    vm.$http.get('/portalMgmt/oa/menus/getPdf', {
                        params: {
                            'type': vm.$route.params.type,
                            'fileName': item,
                            'workTitle': vm.subTitle,
                            'workItemID': vm.$route.params.workItemID,
                            'resultType': 'queryAttachByWorkItemIDResult2',
                            'random': Math.floor(Math.random() * (100000 + 1))
                        }
                    }).then((res) => {
                        vm.file_url[index] = res.data.jsonData;
                        vm.setHandleInfo({ file_url: vm.file_url });
                    }).catch(error => {
                        // vm.$dialog.toast({
                        //     mes: '系统正在开小差，请稍后再试',
                        //     timeout: 1500
                        // });
                        console.log(error);
                        console.log(error);
                    });
                }
            });
        },
        Render(data) {
            let vm = this;
            // 判断是否已经处理
            if (data.CurrectPersonID && data.CurrectPersonID != vm.userInfo.userid) {
                vm.$dialog.toast({
                    mes: '该流程已处理',
                    timeout: 2000,
                    callback: () => {
                        setTimeout(function() {
                            vm.$router.push('/handle');
                        })
                    }
                });
                return false;
            }
            // 详情
            function detail1(data) {
                vm.subTitle = data.subject;
                vm.miji = data.miji;
                vm.huanji = data.huanji;
                vm.leader = data.zusonglingdao;
                vm.xiebanopinions = data.xiebanopinions;
                vm.chuanyueopinions = data.chuanyueopinions;
                if (data.bmapproveopinions == null || data.bmapproveopinions == []) {
                    vm.bmapproveopinions = '';
                } else {
                    vm.bmapproveopinions = data.bmapproveopinions;
                }
                if (data.bmshiopinions == null || data.bmshiopinions == []) {
                    vm.bmshiopinions = '';
                } else {
                    vm.bmshiopinions = data.bmshiopinions;
                }
                if (data.bmnibanopinions == null || data.bmnibanopinions == []) {
                    vm.bmnibanopinions = '';
                } else {
                    vm.bmnibanopinions = data.bmnibanopinions;
                }
                vm.laiwendanwei = data.laiwendanwei;
                vm.shouwenriqi = vm.getDay(data.shouwenriqi);
                vm.zhubanbumen = data.zhubanbumen;
                vm.xiebanbumen = data.xiebanbumen;
                vm.chuanyuebumen = data.chuanyuebumen;
                vm.chengbanqixian = vm.getDay(data.chengbanqixian);
                vm.files = data.files;
                data.bgsnibanopinions.forEach((item, index) => {
                    if (item.neirong == '') {
                        data.bgsnibanopinions[index - 1].userName1 = `${data.bgsnibanopinions[index - 1].userName1}/${item.userName1}`;
                        data.bgsnibanopinions.splice(index, 1);
                    }
                });
                vm.bgsnibanopinions = data.bgsnibanopinions
                vm.zbbmOpinions = data.zbbmopinions;
                vm.xiebanopinions = data.xiebanopinions;
                vm.chuanyueopinions = data.chuanyueopinions;
                vm.caibumen = data.caibumen;
                vm.cainayijian = data.cainayijian;
                vm.caipeople = data.caipeople;
                vm.caishijian = data.caishijian;
                vm.wenhao = data.wenhao;
                if (data.files.length > 1) {
                    vm.fileLength = 1;
                }
                // 发送审批判断
                for (let key in data.nextBtn) {
                    if (key == 'doApprove') {
                        if (data.nextBtn.doApprove.type == '2') {
                            vm.approveFlag = true;
                        }
                    } else if (key == 'doFinishInner') {
                        // 当 nextBtn 为doFinishInner  时，是部内处理 环节；页面 为审批页面
                        vm.approveFlag = true;
                    } else {
                        if (key == 'zgsSecretaryAppActivity') {
                            vm.approveFlag = true;
                        }
                    }
                }
                data.workId = vm.$route.params.workItemID;
                // 领导
                vm.setHandleInfo({ nextBtn: data.nextBtn, keepData: data });
                vm.seePower();
            }
            detail1(data);
            // 发送审批判断
            if (vm.$route.params.activeId == 'zjgw_sp_activity' || vm.$route.params.activeId == 'ldsp_Activity' || vm.$route.params.activeId == 'fgs_ldqf_activity') {
                vm.approveFlag = true;
            }
            if (vm.$route.params.activeId == 'wjsh_activity') {
                vm.toggleFlag = true;
                vm.setHandleInfo({ doApprove: 'toggle' });
            }
        },
        // 正文
        page_jump1(url, isImg = 'notImg') {
            const vm = this;
            if (vm.$route.params.isDO != 1) {
                var urls = vm.isIOS == true ? url : '/pdfjs/web/viewer.html?file=' + url;
                window.location.href = urls;
            }
        },
        page_jump(detailUrl, isImg = 'notImg') {
            const vm = this;
            if (vm.$route.params.isDO != 1) {
                if (detailUrl.fileType != '.pdf') {
                    let index1 = detailUrl.pdfUrl.lastIndexOf('.') + 1;
                    let index2 = detailUrl.pdfUrl.length;
                    let txtName = detailUrl.pdfUrl.substring(index1, index2);
                    if (txtName != 'pdf') {
                        window.location.href = detailUrl.pdfUrl;
                    } else {
                        var urls = vm.isIOS == true ? (detailUrl.pdfUrl.substring(0, detailUrl.pdfUrl.lastIndexOf('.')) + detailUrl.fileType) : '/pdfjs/web/viewer.html?file=' + detailUrl.pdfUrl;
                        window.location.href = urls;
                    }
                    // var urls = vm.isIOS == true ? (detailUrl.pdfUrl.substring(0, detailUrl.pdfUrl.lastIndexOf('.')) + detailUrl.fileType) : '/pdfjs/web/viewer.html?file=' + detailUrl.pdfUrl;
                    // window.location.href = urls;
                } else {
                    var urls2 = vm.isIOS == true ? detailUrl.pdfUrl : '/pdfjs/web/viewer.html?file=' + detailUrl.pdfUrl;
                    window.location.href = urls2;
                }
            }
        },
        queryText(index) {
            var vm = this;
            let detailUrl = vm.file_url[index];
            if (!detailUrl) {
                vm.$dialog.toast({
                    mes: '附件较大，正在加载中，请稍候',
                    timeout: 1500
                });
            } else if (detailUrl == '.rar') {
                vm.$dialog.toast({
                    mes: '目前暂不支持rar文件查看',
                    timeout: 2000
                });
            } else if (detailUrl == '.zip') {
                vm.$dialog.toast({
                    mes: '目前暂不支持zip文件查看',
                    timeout: 2000
                });
            } else {
                vm.page_jump(detailUrl.pdfUrl);
            }
        },
        setNavigationBarTitle(title) {
            UPCHAT.M.NAPI.setNavigationBarTitle(title);
        },
        textDialog() {
            this.$dialog.toast({
                mes: '正在加载中,请稍后再点',
                timeout: 1000
            });
        },
        getDay(date) {
            if (date == '' || date == null || date == 'null' || date == 'undefined') {
                return;
            }
            date = date.replace(/-/g, '/');
            return (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '日';
        },
        setFlag(type) {
            // 0批阅， 1是发送
            if (type == 0) {
                this.piyueFlag = true;
                this.sendFlag = false;
                this.mainFlag = false;
                this.liuzhuanNum++;
            } else if (type == 1) {
                this.sendFlag = true;
                this.mainFlag = false;
                this.piyueFlag = false;
                this.shenpiNum++;
            } else {
                this.mainFlag = true;
                this.sendFlag = false;
                this.piyueFlag = false;
            }
        }
    }

}
</script>
<style lang="less" scoped>
.yd-tabbar-item {
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
}

.handle_main .file_box .file {
    width: 4.3rem;
    color: #4aaeff;
}

.unionCot {
    background: #fff;
}

.yd-tabbar .yd-tabbar-item:first-child {
    border-right: 1px solid #cacaca;
}

.yd-tabbar-txt {
    font-size: 0.3rem;
}

.minTabContent {
    height: auto !important;
    min-height: 1.9rem !important;
}

.nowrap {
    white-space: nowrap;
}

.handle_main .otherRange {
    padding-right: 0.2rem;
    white-space: nowrap;
}

.handle_main .hint {
    padding-top: 0.27rem;
}

.scrollView {
    background: #fff;
}

.flex {
    display: flex;
}

.fluidWidth {
    padding-top: 0;
}

.mainContentFluid {
    margin-top: 0;
}

.workingHours {
    padding-left: 0.15rem;
}

.leader {
    width: 100%;
}

.range_txt {
    min-height: 0.28rem;
    text-align: justify;
}

.noBox {
    height: 4rem;
}

.noMessage {
    text-align: center;
    margin-top: 0.7rem;
    img {
        width: 2.16rem;
    }
    p {
        margin-top: 0.3rem;
        font-size: 0.3rem;
    }
}

.comein {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9;
}

.attachment {
    color: #1381dd;
    max-width: 7.5rem;
    float: left;
    margin: 0;
    width: auto;
    margin-bottom: 0.1rem;
}

.technique {
    color: #1381dd;
    font-weight: bold;
    max-width: 7.5rem;
}

.leaderAdvice .yd-flexbox {
    position: relative;
}

.label_box {
    position: relative;
}

.issueTime {
    float: right;
    margin-top: 0;
}

.range {
    white-space: nowrap;
}

.issueName {
    margin-left: 4rem;
    margin-top: 0;
    color: #0a0a0a;
    margin-bottom: 0.1rem;
    height: 0.3rem;
    line-height: 0.3rem;
}
</style>
<template>
    <yd-layout :class="'unionCot'">
        <yd-navbar title="部门收文" bgcolor="#70b6ef" color="#fff" fontsize="0.36rem">
            <router-link to="/" slot="left">
                <yd-navbar-back-icon color="#fff">
                    <slot color="#fff">返回</slot>
                </yd-navbar-back-icon>
            </router-link>
        </yd-navbar>
        <div class="mainContent handle_main new_label" v-show="mainFlag">
            <div class="mainContentFluid">
                <div class="fluidWidth leaderAdvice">
                    <yd-flexbox direction="vertical">
                        <yd-flexbox-item :id="'scroll_box'">
                            <yd-flexbox>
                                <div class="hint" v-text='subTitle'></div>
                            </yd-flexbox>
                            <yd-flexbox v-if="huanji != '' || miji != ''">
                                <div class="btn_label">
                                    <span class="jijian" v-if="huanji != ''" v-text="huanji"></span><span class="shangmi" v-if="miji != ''" v-text="miji"></span>
                                </div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                    </yd-flexbox>
                    <yd-flexbox direction="vertical" :class="'hasBor'">
                        <yd-flexbox-item :class="'bg_e2'">
                            <ul class="tab_box">
                                <li @click="tab_index = 0" v-bind="{ id : isLeader ? 'js_gaiyao_lead' : 'js_gaiyao' }">
                                    <img src="../../../static/images/label/label_txt.png">
                                    <p>来文信息</p>
                                </li>
                                <li v-bind="{ id : isLeader ? 'js_maintext_lead' : 'js_maintext' }">
                                    <a @click="($route.params.isDo != 1 && maintext == 'javascript:;')  ? textDialog() : (!seePowerFlag) ? maintext = 'javascript:;' : page_jump(maintext);powerDialog();">
                                        <img v-if="!seePowerFlag" src="../../../static/images/label/label_pdf_shadow.png">
                                        <img v-else src="../../../static/images/label/label_pdf.png">
                                        <p>正 文&nbsp;</p>
                                    </a>
                                </li>
                                <li v-bind="{ id : isLeader ? 'js_file_lead' : 'js_file' }" @click="files.length > 0 && seePowerFlag ? tab_index = 2 : tab_index = 0;powerDialog();">
                                    <img v-if="files.length > 0 && seePowerFlag" src="../../../static/images/label/label_doc.png">
                                    <img v-else src="../../../static/images/label/label_doc_shadow.png">
                                    <p> 附 件</p>
                                </li>
                            </ul>
                        </yd-flexbox-item>
                        <yd-flexbox-item>
                            <div class="tab_content bg_box minTabContent" v-if="tab_index == 0">
                                <yd-flexbox>
                                    <div class="range">来文单位：</div>
                                    <div class="range_txt" style="color:#333;" v-text='laiwendanwei'></div>
                                </yd-flexbox>
                                <yd-flexbox>
                                    <div class="range">来文时间：</div>
                                    <div class="range_txt" style="color:#333;" v-text='shouwenriqi'></div>
                                </yd-flexbox>
                                <yd-flexbox>
                                    <div class="range">文&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;号：</div>
                                    <div class="range_txt" style="color:#333;" v-text='wenhao'></div>
                                </yd-flexbox>
                            </div>
                            <div class="tab_content bg_box bg_box2" v-if="tab_index == 2" :class="{active: !fileHidden }">
                                <div class="over">
                                    <div class="range_txt file" v-for='(file, index) in files' @click="queryText(index)" v-text="file"></div>
                                    <a class="open" v-show="files.length > 3" @click="fileHidden = !fileHidden">{{ fileHidden ? '展开附件' : '收起附件'}}</a>
                                </div>
                            </div>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="bmapproveopinions.length > 0">
                            <yd-flexbox>
                                <div class="range">部门领导批示：</div>
                            </yd-flexbox>
                            <yd-flexbox>
                                <div class="opinionOwnBox">
                                    <div class="label_box" v-for="item in bmapproveopinions">
                                        <div class="range_txt flex">
                                            <div class="opName fl nowrap" v-if="item.userName1 != ''" v-text='item.userName1 + "："'></div>
                                            <div class="fl opNeirong" v-text="item.neirong"></div>
                                        </div>
                                        <div class="range opTime" v-text='getDay(item.submitDate)'></div>
                                    </div>
                                </div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="bmnibanopinions.length > 0">
                            <yd-flexbox>
                                <div class="range">综合室拟办：</div>
                            </yd-flexbox>
                            <yd-flexbox>
                                <div class="opinionOwnBox">
                                    <div class="label_box" v-for="item in bmnibanopinions">
                                        <div class="range_txt flex">
                                            <div class="opName fl nowrap" v-if="item.userName1 != ''" v-text='item.userName1 + "："'></div>
                                            <div class="fl opNeirong" v-text="item.neirong"></div>
                                        </div>
                                        <div class="range opTime" v-text='getDay(item.submitDate)'></div>
                                    </div>
                                </div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="bmshiopinions.length > 0">
                            <yd-flexbox>
                                <div class="range">室意见：</div>
                            </yd-flexbox>
                            <yd-flexbox>
                                <div class="opinionOwnBox">
                                    <div class="label_box" v-for="item in bmshiopinions">
                                        <div class="range_txt flex">
                                            <div class="opName fl nowrap" v-if="item.userName1 != ''" v-text='item.userName1 + "："'></div>
                                            <div class="fl opNeirong" v-text="item.neirong"></div>
                                        </div>
                                        <div class="range opTime" v-text='getDay(item.submitDate)'></div>
                                    </div>
                                </div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item :class="'noBox'" v-if="bmshiopinions.length <= 0 && bmnibanopinions.length <= 0 && bmapproveopinions.length <= 0">
                            <div class="noMessage"><img src="../../../static/images/noMessage.png">
                                <p>暂无领导批示</p>
                            </div>
                        </yd-flexbox-item>
                    </yd-flexbox>
                </div>
            </div>
        </div>
        <div class="mainContent1" v-show="sendFlag">
            <fill-postscript v-if="shenpiNum > 0 && !branchFlag" v-on:botSetflag="setFlag" :title="subTitle" :num="shenpiNum"></fill-postscript>
            <branch-Fillpostscript v-if="shenpiNum > 0 && branchFlag" v-on:botSetflag="setFlag" :num="shenpiNum"></branch-Fillpostscript>
        </div>
        <div class="mainContent1" v-show="piyueFlag">
            <assist-approve v-if="liuzhuanNum > 0" v-on:botSetflag="setFlag" :title="subTitle" :num="liuzhuanNum"></assist-approve>
        </div>
        <yd-tabbar v-if="$route.params.isDo == 0 && mainFlag" :class="'skip_box'" slot="tabbar" bgcolor="#61b3f6" color="#fff" size=".3rem">
            <div class="skip_btn">
                <a id="js_piyue_lead" v-if="approveFlag && toggleFlag" @click="setFlag(1)">
                    <div class="">批&nbsp;&nbsp;&nbsp;&nbsp;阅</div>
                </a>
                <a id="js_piyue_lead" v-else-if="!approveFlag && toggleFlag" @click="setFlag(1)">
                    <div class="">批&nbsp;&nbsp;&nbsp;&nbsp;阅</div>
                </a>
                <a id="js_piyue_lead" v-else-if="approveFlag && !toggleFlag" @click="setFlag(0)">
                    <div class="">批&nbsp;&nbsp;&nbsp;&nbsp;阅</div>
                </a>
                <a v-else id="js_fasong" @click="setFlag(1)">
                    <div class="">发&nbsp;&nbsp;&nbsp;&nbsp;送</div>
                </a>
            </div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex';
import fillPostscript from './fillPostscript.vue';
import branchFillpostscript from './branchFillPostscript.vue';
import assistApprove from '../assist/assistApprove.vue';
import axios from 'axios';
export default {
    name: 'unionPayReceived',
    components: {
        fillPostscript,
        assistApprove,
        branchFillpostscript
    },
    data() {
        return {
            subTitle: '',
            miji: '',
            huanji: '',
            summary: '',
            opinion_index: 0,
            textHidden: true,
            fileHidden: true,
            tab_index: 0,
            approveFlag: false,
            laiwendanwei: '',
            shouwenriqi: '',
            zhubanbumen: '',
            xiebanbumen: '',
            chuanyuebumen: '',
            xiebanopinions: [],
            bmshiopinions: [], // 室意见
            bmnibanopinions: [], // 综合室
            chuanyueopinions: [],
            chengbanqixian: '',
            files: '',
            toggleFlag: false,
            navflag: false,
            fileLength: 0,
            shenpiNum: 0,
            maintext: 'javascript:;',
            liuzhuanNum: 0,
            bmapproveopinions: [],
            zjgwopinions: [],
            bgsnibanopinions: [],
            zbbmOpinions: [],
            file_url: [],
            isLeader: false,
            mainFlag: true, // 显示主页面
            sendFlag: false, // 显示发送页面
            piyueFlag: false, // 显示批阅页面
            cainayijian: '', // 办理结果
            branchFlag: false, // 是否是分公司
            seePowerFlag: true,
            wenhao: '',
            caibumen: '',
            caipeople: '',
            caishijian: ''
        }
    },
    mounted() {
        let vm = this;
        vm.pageRender();
        vm.seePower();
        vm.setTitleName('部门收文');
        document.setTitle('部门收文');
        setTimeout(function() {
            var title = '部门收文';
            if (UPCHAT.M.isNative()) {
                UPCHAT.M.init(vm.setNavigationBarTitle(title));
            }
        }, 200);
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo',
            handleInfo: 'getHandleInfo'
        }),
        ...mapGetters(['getNewsInfo'])
    },
    methods: {
        ...mapMutations([
            'setHandleInfo', 'setTitleName'
        ]),
        pageRender() {
            var vm = this;
            if (Number(vm.userInfo.role) == 2) {
                vm.isLeader = true;
            }
            // 判断是否是分公司签报的发送页面
            vm.userInfo.deptname.indexOf('分公司') == -1 ? vm.branchFlag = false : vm.branchFlag = true;
            vm.setHandleInfo({ activityDefId: vm.$route.params.activeId, keepAlive: [], doApprove: '' });
            const promise1 = new Promise(function(resolve, reject) {
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryForm', {
                    'type': vm.$route.params.type,
                    'workItemID': vm.$route.params.workItemID,
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
                }, function(data1) {
                    if (data1.jsonData.CURRENTSTATE == 10 || vm.$route.params.isDo == 1) {
                        vm.Render(data1.jsonData);
                        resolve(true);
                    } else {
                        vm.$dialog.toast({
                            mes: '该流程已被处理',
                            timeout: 2000,
                            callback: () => {
                                setTimeout(function() {
                                    vm.$router.push('/handle');
                                })
                            }
                        });
                    }
                });
            });
            promise1.then(function(data) {
                vm.loadText();
                vm.loadFile();
            });
        },
        loadText() {
            const vm = this;
            // 阅读正文附件接口
            axios.get('/portalMgmt/oa/menus/getPdf', {
                params: {
                    'type': vm.$route.params.type,
                    'resultType': 'queryDocByWorkItemIDResult',
                    'workItemID': vm.$route.params.workItemID,
                    'workTitle': vm.subTitle,
                    'random': Math.floor(Math.random() * (100000 + 1))
                }
            }).then(function(data4) {
                vm.maintext = data4.data.jsonData == null ? 'javascript:;' : data4.data.jsonData.pdfUrl;
                vm.setHandleInfo({ mainText: vm.maintext });
            });
        },
        loadFile() {
            let vm = this;
            vm.files.forEach(function(item, index) {
                if (item.indexOf('.rar') != -1) {
                    vm.file_url[index] = '.rar';
                    vm.setHandleInfo({ file_url: vm.file_url });
                } else if (item.indexOf('.zip') != -1) {
                    vm.file_url[index] = '.zip';
                    vm.setHandleInfo({ file_url: vm.file_url });
                } else {
                    vm.$http.get('/portalMgmt/oa/menus/getPdf', {
                        params: {
                            'type': vm.$route.params.type,
                            'fileName': item,
                            'workTitle': vm.subTitle,
                            'workItemID': vm.$route.params.workItemID,
                            'resultType': 'queryAttachByWorkItemIDResult2',
                            'random': Math.floor(Math.random() * (100000 + 1))
                        }
                    }).then((res) => {
                        vm.file_url[index] = res.data.jsonData;
                        vm.setHandleInfo({ file_url: vm.file_url });
                    }).catch(error => {
                        // vm.$dialog.toast({
                        //     mes: '系统正在开小差，请稍后再试',
                        //     timeout: 1500
                        // });
                        console.log(error);
                    });
                }
            });
        },
        Render(data) {
            let vm = this;
            // 判断是否已经处理
            if (data.CurrectPersonID && data.CurrectPersonID != vm.userInfo.userid) {
                vm.$dialog.toast({
                    mes: '该流程已处理',
                    timeout: 2000,
                    callback: () => {
                        setTimeout(function() {
                            vm.$router.push('/handle');
                        })
                    }
                });
                return false;
            }
            // 详情
            function detail1(data) {
                vm.subTitle = data.subject;
                vm.miji = data.miji;
                vm.huanji = data.huanji;
                vm.leader = data.zusonglingdao;
                vm.xiebanopinions = data.xiebanopinions;
                vm.chuanyueopinions = data.chuanyueopinions;
                if (data.bmapproveopinions == null || data.bmapproveopinions == []) {
                    vm.bmapproveopinions = '';
                } else {
                    vm.bmapproveopinions = data.bmapproveopinions;
                }
                if (data.bmshiopinions == null || data.bmshiopinions == []) {
                    vm.bmshiopinions = '';
                } else {
                    vm.bmshiopinions = data.bmshiopinions;
                }
                if (data.bmnibanopinions == null || data.bmnibanopinions == []) {
                    vm.bmnibanopinions = '';
                } else {
                    vm.bmnibanopinions = data.bmnibanopinions;
                }
                vm.laiwendanwei = data.laiwendanwei;
                vm.shouwenriqi = vm.getDay(data.shouwenriqi);
                vm.zhubanbumen = data.zhubanbumen;
                vm.xiebanbumen = data.xiebanbumen;
                vm.chuanyuebumen = data.chuanyuebumen;
                vm.chengbanqixian = vm.getDay(data.chengbanqixian);
                vm.files = data.files;
                data.bgsnibanopinions.forEach((item, index) => {
                    if (item.neirong == '') {
                        data.bgsnibanopinions[index - 1].userName1 = `${data.bgsnibanopinions[index - 1].userName1}/${item.userName1}`;
                        data.bgsnibanopinions.splice(index, 1);
                    }
                });
                vm.bgsnibanopinions = data.bgsnibanopinions
                vm.zbbmOpinions = data.zbbmopinions;
                vm.xiebanopinions = data.xiebanopinions;
                vm.chuanyueopinions = data.chuanyueopinions;
                vm.caibumen = data.caibumen;
                vm.cainayijian = data.cainayijian;
                vm.caipeople = data.caipeople;
                vm.caishijian = data.caishijian;
                vm.wenhao = data.wenhao;
                if (data.files.length > 1) {
                    vm.fileLength = 1;
                }
                // 发送审批判断
                for (let key in data.nextBtn) {
                    if (key == 'doApprove') {
                        if (data.nextBtn.doApprove.type == '2') {
                            vm.approveFlag = true;
                        }
                    } else if (key == 'doFinishInner') {
                        // 当 nextBtn 为doFinishInner  时，是部内处理 环节；页面 为审批页面
                        vm.approveFlag = true;
                    } else {
                        if (key == 'zgsSecretaryAppActivity') {
                            vm.approveFlag = true;
                        }
                    }
                }
                // 领导
                vm.setHandleInfo({ nextBtn: data.nextBtn, keepData: data });
            }
            detail1(data);
            // 发送审批判断
            if (vm.$route.params.activeId == 'zjgw_sp_activity' || vm.$route.params.activeId == 'ldsp_Activity' || vm.$route.params.activeId == 'fgs_ldqf_activity') {
                vm.approveFlag = true;
            }
            if (vm.$route.params.activeId == 'wjsh_activity') {
                vm.toggleFlag = true;
                vm.setHandleInfo({ doApprove: 'toggle' });
            }
        },
        page_jump(url, isImg = 'notImg') {
            const vm = this;
            if (vm.$route.params.isDO != 1) {
                var urls = vm.isIOS == true ? url : '/pdfjs/web/viewer.html?file=' + url;
                window.location.href = urls;
            }
        },
        queryText(index) {
            var vm = this;
            let detailUrl = vm.file_url[index];
            if (!detailUrl) {
                vm.$dialog.toast({
                    mes: '附件较大，正在加载中，请稍候',
                    timeout: 1500
                });
            } else if (detailUrl == '.rar') {
                vm.$dialog.toast({
                    mes: '目前暂不支持rar文件查看',
                    timeout: 2000
                });
            } else if (detailUrl == '.zip') {
                vm.$dialog.toast({
                    mes: '目前暂不支持zip文件查看',
                    timeout: 2000
                });
            } else {
                if (detailUrl.pdfUrl.indexOf('.pdf') != -1) {
                    vm.page_jump(detailUrl.pdfUrl);
                } else {
                    window.location.href = detailUrl.pdfUrl;
                }
            }
        },
        setNavigationBarTitle(title) {
            UPCHAT.M.NAPI.setNavigationBarTitle(title);
        },
        textDialog() {
            this.$dialog.toast({
                mes: '正在加载中,请稍后再点',
                timeout: 1000
            });
        },
        getDay(date) {
            if (date == '' || date == null || date == 'null' || date == 'undefined') {
                return;
            }
            date = date.replace(/-/g, '/');
            return (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '日';
        },
        // 已办列表需要按权限查看附件和正文
        seePower() {
            const vm = this;
            // 对于没有“密级”的流程，给秘书（当前环节事务助理）和公司领导 可以查看正文和附件；
            if (vm.$route.params.isDo == 1) {
                if (vm.miji != '') {
                    vm.seePowerFlag = false;
                } else {
                    if (vm.handleInfo.activityDefId == 'zgsSecretaryAppActivity' || vm.handleInfo.activityDefId == 'fwZgsSecretaryApproval' || vm.handleInfo.activityDefId == 'zgs_ldmsqf_activity' || vm.userInfo.role == 2) {
                        vm.seePowerFlag = true;
                    } else {
                        vm.seePowerFlag = false;
                    }
                }
            }
        },
        // 按权限查看附件和正文提示
        powerDialog() {
            const vm = this;
            if (!vm.seePowerFlag) {
                vm.$dialog.toast({
                    mes: '由于安全保密要求，已办文件暂不支持正文和附件查看！',
                    timeout: 1500
                });
            }
        },
        setFlag(type) {
            // 0批阅， 1是发送
            if (type == 0) {
                this.piyueFlag = true;
                this.sendFlag = false;
                this.mainFlag = false;
                this.liuzhuanNum++;
            } else if (type == 1) {
                this.sendFlag = true;
                this.mainFlag = false;
                this.piyueFlag = false;
                this.shenpiNum++;
            } else {
                this.mainFlag = true;
                this.sendFlag = false;
                this.piyueFlag = false;
            }
        }
    }

}
</script>
<style lang="less" scoped>
.yd-tabbar-item {
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
}

.handle_main .file_box .file {
    width: 4.3rem;
    color: #4aaeff;
}

.unionCot {
    background: #fff;
}

.yd-tabbar .yd-tabbar-item:first-child {
    border-right: 1px solid #cacaca;
}

.yd-tabbar-txt {
    font-size: 0.3rem;
}

.minTabContent {
    height: auto !important;
    min-height: 1.9rem !important;
}

.nowrap {
    white-space: nowrap;
}

.handle_main .otherRange {
    padding-right: 0.2rem;
    white-space: nowrap;
}

.handle_main .hint {
    padding-top: 0.27rem;
}

.scrollView {
    background: #fff;
}

.flex {
    display: flex;
}

.fluidWidth {
    padding-top: 0;
}

.mainContentFluid {
    margin-top: 0;
}

.workingHours {
    padding-left: 0.15rem;
}

.leader {
    width: 100%;
}

.range_txt {
    min-height: 0.28rem;
    text-align: justify;
}

.noBox {
    height: 4rem;
}

.noMessage {
    text-align: center;
    margin-top: 0.7rem;
    img {
        width: 2.16rem;
    }
    p {
        margin-top: 0.3rem;
        font-size: 0.3rem;
    }
}

.comein {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9;
}

.attachment {
    color: #1381dd;
    max-width: 7.5rem;
    float: left;
    margin: 0;
    width: auto;
    margin-bottom: 0.1rem;
}

.technique {
    color: #1381dd;
    font-weight: bold;
    max-width: 7.5rem;
}

.leaderAdvice .yd-flexbox {
    position: relative;
}

.label_box {
    position: relative;
}

.issueTime {
    float: right;
    margin-top: 0;
}

.range {
    white-space: nowrap;
}

.issueName {
    margin-left: 4rem;
    margin-top: 0;
    color: #0a0a0a;
    margin-bottom: 0.1rem;
    height: 0.3rem;
    line-height: 0.3rem;
}
</style>


